import React, { Component } from 'react'
import BraftEditor from 'braft-editor'
import 'braft-editor/dist/index.css'
import CustomBreadcrumb from '@/components/CustomBreadcrumb'
import { Layout, Divider } from 'antd'
import '@/style/view-style/editor.scss'

class EditorView extends Component {
    state = {
        editorState: BraftEditor.createEditorState('<p> <b>!</b></p>'),
        outputHTML: '<p> <b>!</b></p>'
    }

    componentDidMount() {
        this.timer = setTimeout(this.setEditorContentAsync, 3000)
    }

    componentWillUnmount() {
        clearTimeout(this.timer)
    }

    handleEditorChange = editorState => {
        this.setState({
            editorState: editorState,
            outputHTML: editorState.toHTML()
        })
    }

    setEditorContentAsync = () => {
        this.setState({
            editorState: BraftEditor.createEditorState(',<b>! !</b>')
        })
    }

    render() {
        const { editorState, outputHTML } = this.state
        return (
            <Layout className='animated fadeIn'>
                <div>
                    <CustomBreadcrumb arr={['', '']}></CustomBreadcrumb>
                </div>
                <div className='base-style'>
                    <h3></h3>
                    <Divider />
                    <p>
                        
                        <a href='https://github.com/margox/braft-editor'>braft-editor</a>
                    </p>
                </div>
                <div className='base-style'>
                    <div className='editor'>
                        <BraftEditor value={editorState} onChange={this.handleEditorChange} />
                    </div>
                </div>
                <div className='base-style'>
                    <h5></h5>
                    <div className='output-content'>{outputHTML}</div>
                </div>
            </Layout>
        )
    }
}

export default EditorView
